$orb: orb;
$errorColor: #F55;
$borderColor: rgb(96, 100, 109);
$shadowColor: #95c3e8;
$shadowDarkColor: #484545;
$fontColor: #fff;
$titleFontColor: rgba(255, 255, 255, .7);
$titleFontSize: 16px;
$hoverHighlightColor: #40a9ff;
$headerHeight: 52px;
$normalBlue: #1890ff;
$disableBlue: rgba(112, 165, 214, 0.7);
$hoverBlue: #40a9ff;
$modalBgColor: rgba(5, 6, 8, .85);
// $modalBgColor: #000;

$hoverWhiteColor: rgba(255, 255, 255, .7);
$listHoverBgColor: rgba(64, 169, 255, 0.28);
$listActiveBgColor: rgba(64, 169, 255, 0.5);

@function getSmallShadow ($color) {
	@return 0 0 11px 0 $color;
}
@function getMiddleShadow ($color) {
	@return 0 0 34px 0 $color;
}
@function getBigShadow ($color) {
	@return 0 0 94px 0 $color;
}

@keyframes orb {
  from {
    transform:rotate(0deg)
  }
  to{
    transform:rotate(-360deg)
  }
}

.comment-item {
	.comment-box {
		display: flex;
		.comment-rich {
			width: 100%;

			.comment-content-detail p {
				margin: 0;
			}
		}
		.comment-avatar {
	    position: relative;
	    -ms-flex-negative: 0;
	    flex-shrink: 0;
	    margin-right: 12px;
	    cursor: pointer;

			img {
		    width: 40px;
		    /*height: 32px;*/
		    /*border-radius: 50%;*/
			}
		}
	
		.comment-content-author {
	    display: flex;
	    flex-wrap: wrap;
	    margin-bottom: 4px;
	    font-size: 14px;
	    align-items: center;
	    color: $fontColor;
	    & > a, 
	    & > span {
		    padding-right: 8px;
		    /*font-size: 14px;*/
		    line-height: 18px;
			}
			& > b {
				padding-right: 8px;
			}
			.comment-content-author-time {
		    color: #ccc;
		    white-space: nowrap;
		    cursor: auto;
			}
		}
	}
}
