$orb: orb;
$errorColor: #F55;
$borderColor: rgb(96, 100, 109);
$shadowColor: #95c3e8;
$shadowDarkColor: #484545;
$fontColor: #fff;
$titleFontColor: rgba(255, 255, 255, .7);
$titleFontSize: 16px;
$hoverHighlightColor: #40a9ff;
$headerHeight: 52px;
$normalBlue: #1890ff;
$disableBlue: rgba(112, 165, 214, 0.7);
$hoverBlue: #40a9ff;
$modalBgColor: rgba(5, 6, 8, .85);
// $modalBgColor: #000;

$hoverWhiteColor: rgba(255, 255, 255, .7);
$listHoverBgColor: rgba(64, 169, 255, 0.28);
$listActiveBgColor: rgba(64, 169, 255, 0.5);

@function getSmallShadow ($color) {
	@return 0 0 11px 0 $color;
}
@function getMiddleShadow ($color) {
	@return 0 0 34px 0 $color;
}
@function getBigShadow ($color) {
	@return 0 0 94px 0 $color;
}

@keyframes orb {
  from {
    transform:rotate(0deg)
  }
  to{
    transform:rotate(-360deg)
  }
}

.site-item {
	transform: scale(0.95);
	padding: 20px;
  width: 100%;
  border-radius: 4px;
  transition: .2s all;
  /*border-bottom: 2px solid #f6f6f6;*/
  box-shadow: getMiddleShadow($shadowDarkColor);
	.site-title {
		font-size: 18px;
    font-weight: 600;
    /*color: #1a1a1a;*/
    color: $fontColor;
    line-height: 1.6;
    margin: 0;
    padding: 5px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
    a {
    	color: orange;
    }
    /* span {
    	cursor: pointer;
    } */
    .anticon-close {
    	cursor: pointer;
    }
	}
	.sit-sub-text {
		margin: .2rem 0;
		line-height: 1;
		color: $fontColor;
	}
	.rich-head {
		display: inline-block;
		overflow: hidden;
		color: $fontColor;
		.anticon svg {
			width: 14px;
		}
		.ant-rate-star:not(:last-child) {
	    margin-right: 3px;
		}
	}
	.rich-content {
		/*cursor: pointer;*/
		/*display: flex;*/
		.rich-content-cover {
			margin-top: 1rem;
			.rich-content-cover-inner {
				display: flex;
				align-items: center;
				justify-content: center;
				img {
					max-width: 40%;
				}

			}
		}

		.rich-content-text {
			/*color: #000;*/
			color: $fontColor;
			width: 100%;
		}
		.rich-content-text:hover {
			/*color: #646464;*/
		}
		.rich-footer {
			margin-top: 1rem;
			color: $fontColor;
			ul {
				margin: 0;
				li {
					/*display: inline-block;*/
					a {
						/*color: $fontColor;*/
						text-decoration: underline;
					}
					a:hover {
						color: $hoverHighlightColor;
					}
				}
				li:not(:last-child) {
					margin-right: 5px;
				}
			}
		}
	}
}
$activeHeart: orange;
.anticon-heart.active {
	color: $activeHeart;
}
/* .anticon-heart.acting {
	animation: heartSpin 1s linear infinite;
} */
.anticon-heart.disabled {
	color: rgba(158, 158, 158, 0.9);
	cursor: not-allowed;
}
.site-item:hover {
	transform: scale(1);
  box-shadow: 1px 1px 94px #afd4f3;
  /*animation: shadow 2s linear infinite;*/

}
.site-item.only-show {
	transform: none;
}
.site-item.only-show:hover {
	box-shadow: getMiddleShadow($shadowDarkColor);
}

