$orb: orb;
$errorColor: #F55;
$borderColor: rgb(96, 100, 109);
$shadowColor: #95c3e8;
$shadowDarkColor: #484545;
$fontColor: #fff;
$titleFontColor: rgba(255, 255, 255, .7);
$titleFontSize: 16px;
$hoverHighlightColor: #40a9ff;
$headerHeight: 52px;
$normalBlue: #1890ff;
$disableBlue: rgba(112, 165, 214, 0.7);
$hoverBlue: #40a9ff;
$modalBgColor: rgba(5, 6, 8, .85);
// $modalBgColor: #000;

$hoverWhiteColor: rgba(255, 255, 255, .7);
$listHoverBgColor: rgba(64, 169, 255, 0.28);
$listActiveBgColor: rgba(64, 169, 255, 0.5);

@function getSmallShadow ($color) {
	@return 0 0 11px 0 $color;
}
@function getMiddleShadow ($color) {
	@return 0 0 34px 0 $color;
}
@function getBigShadow ($color) {
	@return 0 0 94px 0 $color;
}

@keyframes orb {
  from {
    transform:rotate(0deg)
  }
  to{
    transform:rotate(-360deg)
  }
}

.right-content {
	margin-left: 10px;
	margin-top: 10px;
	width: 256px;
	.nav-list {
		.ant-menu:not(.ant-menu-horizontal) .ant-menu-item-selected {
			background: $listHoverBgColor;
		}
		.title {
			padding: 5px 10px;
		}
	}

	.nav-list:not(:last-child) {
		margin-bottom: 15px;
	}
	.search-btn {
		margin-bottom: 25px;
		padding: 0 5px;
	}
}

.top10-list {
	ul {
		margin: 0;
		padding: 5px 10px;
	}
}

.newest-comment-wrapper.comment-container {
	.comment-list > .comment-item {
		padding: 10px;
		box-shadow: none;
		&:not(:last-child) {
			margin-bottom: 0;
		}
	}
} 

.top-notice {
	padding: 0 10px;
	margin-bottom: 15px;

	.time {
		color: #ccc;
		font-size: 12px;
	}
}