$orb: orb;
$errorColor: #F55;
$borderColor: rgb(96, 100, 109);
$shadowColor: #95c3e8;
$shadowDarkColor: #484545;
$fontColor: #fff;
$titleFontColor: rgba(255, 255, 255, .7);
$titleFontSize: 16px;
$hoverHighlightColor: #40a9ff;
$headerHeight: 52px;
$normalBlue: #1890ff;
$disableBlue: rgba(112, 165, 214, 0.7);
$hoverBlue: #40a9ff;
$modalBgColor: rgba(5, 6, 8, .85);
// $modalBgColor: #000;

$hoverWhiteColor: rgba(255, 255, 255, .7);
$listHoverBgColor: rgba(64, 169, 255, 0.28);
$listActiveBgColor: rgba(64, 169, 255, 0.5);

@function getSmallShadow ($color) {
	@return 0 0 11px 0 $color;
}
@function getMiddleShadow ($color) {
	@return 0 0 34px 0 $color;
}
@function getBigShadow ($color) {
	@return 0 0 94px 0 $color;
}

@keyframes orb {
  from {
    transform:rotate(0deg)
  }
  to{
    transform:rotate(-360deg)
  }
}

.face-container {
  margin-right: 20px;
  display: flex;
  flex-direction: row-reverse;
  .img-preview {
    width: 112px;
    height: 112px;
    overflow: hidden;

    &.small {
      width: 40px;
      height: 40px;
      margin-top: 20px;
    }
  }
  .show-ctn {
    padding-top: 25px;
    .opera-bottom {
      margin-top: 30px;
      .ant-btn:not(:last-child) {
        margin-right: 15px;
      }
    }
  }
  .show-org-img {
    img {
      width: 100%;
    }
  }
  .preview-ctn {
    margin-left: 20px;
  }
  .face-wrapper {
    display: flex;
    .cropper-ctn {
    	position: relative;
    	.cropper-bg.self-bg {
    		position: absolute;
    		left: 0;
    		top: 0;
    		width: 100%;
    		height: 100%;
    		&::after {
    			content: '';
    			display: block;
    			width: 100%;
    			height: 100%;
    			background: rgba(0, 0, 0, .25);
    		}
    	}
    }
    .preview-ctn {
      color: $fontColor;
    }
  }
}