$orb: orb;
$errorColor: #F55;
$borderColor: rgb(96, 100, 109);
$shadowColor: #95c3e8;
$shadowDarkColor: #484545;
$fontColor: #fff;
$titleFontColor: rgba(255, 255, 255, .7);
$titleFontSize: 16px;
$hoverHighlightColor: #40a9ff;
$headerHeight: 52px;
$normalBlue: #1890ff;
$disableBlue: rgba(112, 165, 214, 0.7);
$hoverBlue: #40a9ff;
$modalBgColor: rgba(5, 6, 8, .85);
// $modalBgColor: #000;

$hoverWhiteColor: rgba(255, 255, 255, .7);
$listHoverBgColor: rgba(64, 169, 255, 0.28);
$listActiveBgColor: rgba(64, 169, 255, 0.5);

@function getSmallShadow ($color) {
	@return 0 0 11px 0 $color;
}
@function getMiddleShadow ($color) {
	@return 0 0 34px 0 $color;
}
@function getBigShadow ($color) {
	@return 0 0 94px 0 $color;
}

@keyframes orb {
  from {
    transform:rotate(0deg)
  }
  to{
    transform:rotate(-360deg)
  }
}

body {
    color: $fontColor;
    p {
        margin: 0;
    }
}
.main-content-wrapper {
    display: flex;
    justify-content: center;
}
.main-content {
    width: 734px;
}
.self-wrapper.flex {
    .self-line {
        display: flex;
        label {
            width: 50px;
        }
        .img-ctn {
            width: 100%;
        }
    }
}
.self-wrapper {
    .self-line {
        .self-text-wrapper {
            width: 100%;
            .bf-container {
                height: auto;
            }
        }
		/*flex-direction: column; */
        label {
            color: #fff;
            display: block;
        }
    
        .img-ctn {
            /*text-align: center;*/
            cursor: pointer;

            img {
                max-width: 90%;
                max-height: 160px;
            }
        }
        .bf-container  {
            width: 100%;
        }
        .inline {
            display: flex;
            flex-direction: column;

            .code {
                background: #fff;
            }
        }

        

    }

    .self-line:not(:last-child) {
        margin-bottom: .8rem;
    }

    .self-line.must {
        label:first-child::after {
            content: '*';
            color: red;
        }
    }
}

.self-modal-wrapper {
    .self-wrapper {
        padding: 24px;
    }
}
.error-tip {
    color: $errorColor;
    padding-top: .1rem;
    font-size: 12px;
}
.self-footer {

    /* padding: 10px 16px;
  text-align: right;
  background: transparent;
  border-top: 1px solid #f0f0f0;
  border-radius: 0 0 2px 2px; */
    button+button {
        margin-bottom: 0;
        margin-left: 8px;
    }
}

.password-tip {
    margin: 0;

    .error {
        color: $errorColor;
    }
}

/**************************/
.ant-menu-root.ant-menu-vertical, .ant-menu-root.ant-menu-vertical-left, .ant-menu-root.ant-menu-vertical-right, .ant-menu-root.ant-menu-inline,
.ant-menu {
    background: transparent;
    box-shadow: getBigShadow($shadowDarkColor);
}
.ant-menu-item:active, .ant-menu-submenu-title:active {
    background: $listActiveBgColor;
}
.ant-checkbox-wrapper,
.ant-menu-item > a {
    color: $fontColor;
}
.ant-menu-inline, .ant-menu-vertical, .ant-menu-vertical-left {
    border: none;
}
.ant-menu-item:hover, .ant-menu-submenu-title:hover {
    background: $listHoverBgColor;
}
.ant-select.error:not(.ant-select-disabled) .ant-select-selector,
.ant-select.error:not(.ant-select-disabled):hover .ant-select-selector,
.ant-input-password.error,
.ant-input-password.error:hover,
.ant-input-password.error:focus,
.ant-input.error,
.ant-input.error:hover,
.ant-input.error:focus {
    border-color: $errorColor;

}

.ant-popover,
.ant-popover .ant-popover-inner,
.ant-btn:hover,
.ant-btn:focus,
.ant-btn:active,
.ant-btn.active,
.ant-select-single:not(.ant-select-customize-input) .ant-select-selector,
.ant-menu.ant-menu-dark,
.ant-modal-content,
.ant-modal-header {
    background: transparent;
}

.ant-select-item-option-selected:not(.ant-select-item-option-disabled),
.ant-select-item-option-active:not(.ant-select-item-option-disabled) {
    background: $listHoverBgColor;
    color: $fontColor;
}

.ant-select-selection-placeholder {
    opacity: .7;
}

.ant-modal-header {
    border-bottom-color: $borderColor;
}

.ant-modal-footer {
    border-top-color: $borderColor;
}

a,
.ant-pagination-jump-prev .ant-pagination-item-container .ant-pagination-item-ellipsis, 
.ant-pagination-jump-next .ant-pagination-item-container .ant-pagination-item-ellipsis,
.ant-menu,
.ant-popover-inner-content,
.ant-dropdown-menu-item,
.ant-dropdown-menu-submenu-title,
.ant-dropdown-menu-item>a,
.ant-dropdown-menu-submenu-title>a,
.ant-pagination-options-quick-jumper input,
.ant-input-password-icon,
.ant-empty-description,
.ant-modal-title,
.ant-modal-close-x,
.ant-select-item,
.ant-select-multiple .ant-select-selector,
.ant-select-multiple .ant-select-selection-item-remove,
.ant-message-notice-content,
.ant-btn,
.ant-input {
    color: $fontColor;
}

.ant-btn-primary {
    color: $normalBlue;
}

.ant-dropdown-menu-item:hover,
.ant-dropdown-menu-submenu-title:hover {
    background: $listHoverBgColor;
}

.ant-dropdown-menu-item>a:hover,
.ant-dropdown-menu-submenu-title>a:hover {
    color: $fontColor;
}

.ant-input-password-icon:hover,
.ant-select-multiple .ant-select-selection-item-remove:hover {
    color: $hoverWhiteColor;
}

.ant-popover .ant-popover-content {
    background: #000;
}

.ant-popover-placement-topLeft>.ant-popover-content>.ant-popover-arrow {
    border-right-color: #000;
    border-bottom-color: #000;
    box-shadow: 3px 3px 7px rgba(255, 255, 255, 0.1);
}

.ant-popover .ant-popover-inner {
    /*box-shadow: 0 3px 6px -4px rgba(149, 195, 232,0.12), 0 6px 16px 0 rgba(149, 195, 232,0.08), 0 9px 28px 8px rgba(149, 195, 232,0.05);*/
    box-shadow: getSmallShadow($shadowColor);
}
.ant-dropdown-menu,
.ant-message-notice-content,
.ant-select-dropdown {
    background: $modalBgColor;
    border-color: $borderColor;
    box-shadow: getSmallShadow($shadowColor);
}

.ant-btn,
.ant-select-multiple .ant-select-selector,
.ant-select-multiple .ant-select-selection-item,
.ant-input {
    background: transparent;
    border-color: $borderColor;
}

.ant-modal-content {
    background: $modalBgColor;
    color: $fontColor;
    box-shadow: getBigShadow($shadowColor);
    animation: box 1s linear infinite;
    border-radius: 4px;
}

.ant-select-single:not(.ant-select-customize-input) .ant-select-selector {
    color: $fontColor;
    border-color: $borderColor;
}

.ant-tooltip-content,

.ant-pagination-options-quick-jumper input,
.ant-input-affix-wrapper {
    background: transparent;
    border-color: $borderColor;
}

.ant-tooltip-content {
    box-shadow: getSmallShadow($shadowColor);
}

.ant-btn-link {
    color: $normalBlue;
    background-color: transparent;
    border-color: transparent;
    box-shadow: none;
}

.ant-spin-container::after {
    background: transparent;
}

.ant-btn-primary {
    color: $fontColor;
    background-color: $normalBlue;
    border-color: $normalBlue;
}

.ant-btn-primary:hover,
.ant-btn-primary:focus {
    color: $fontColor;
    background-color: $hoverBlue;
    border-color: $hoverBlue
}

.ant-dropdown-menu-item>a:hover,
.ant-dropdown-menu-submenu-title>a:hover {
    color: $normalBlue;
}

.ant-dropdown-menu-item>.link-a,
.ant-dropdown-menu-submenu-title>.link-a {
    display: block;
}

.ant-btn-link[disabled],
.ant-btn-link[disabled]:hover {
    color: rgba(255, 255, 255, .25)
}

.ant-btn-primary-disabled,
.ant-btn-primary.disabled,
.ant-btn-primary[disabled],
.ant-btn-primary-disabled:hover,
.ant-btn-primary.disabled:hover,
.ant-btn-primary[disabled]:hover,
.ant-btn-primary-disabled:focus,
.ant-btn-primary.disabled:focus,
.ant-btn-primary[disabled]:focus,
.ant-btn-primary-disabled:active,
.ant-btn-primary.disabled:active,
.ant-btn-primary[disabled]:active,
.ant-btn-primary-disabled.active,
.ant-btn-primary.disabled.active,
.ant-btn-primary[disabled].active {
	color: rgba(255, 255, 255, .25);
	border-color: $disableBlue;
	background-color: $disableBlue;
}
.ant-popover-inner-content {
	font-size: 12px;
	padding: 8px 12px;
}
@keyframes box {
    from {
        box-shadow: getBigShadow($shadowColor);
    }

    50% {
        box-shadow: 0 0 94px 10px $shadowColor;
    }

    to {
        box-shadow: getBigShadow($shadowColor);
    }
}
.ant-pagination {
    color: $fontColor;
    margin-top: 1rem;
    .ant-pagination-item-link {
        color: $fontColor;
    }
    .ant-pagination-prev,
    .ant-pagination-next {
        background: transparent;
    }
    .ant-pagination-disabled a {
        color: rgba(255, 255, 255, .6);
    }
    .ant-pagination-item a {
        color: $fontColor;
    }
    .ant-pagination-item-active {
        background: transparent;
        border-color: transparent;
        a {
            color: #1890ff;
        }
    }
}
.ant-pagination-disabled a, 
.ant-pagination-disabled:hover a, 
.ant-pagination-disabled:focus a, 
.ant-pagination-disabled .ant-pagination-item-link, 
.ant-pagination-disabled:hover .ant-pagination-item-link, 
.ant-pagination-disabled:focus .ant-pagination-item-link {
    color: rgba(255, 255, 255, .25);
}
/**/
.container {
    padding-top: $headerHeight + 10px;
    color: $fontColor;
    /*position: relative;*/
    /*z-index: 2;*/
}

.max-container {
    width: 1000px;
}
.align-right {
    text-align: right;
}
.link-a {
    cursor: pointer;
    color: $fontColor;
}

.link-to {
    text-decoration: underline;
}
.link-to:hover {
    text-decoration: underline;
}

.link-a:hover {
    color: $normalBlue;
}
.bf-content {
    color: $fontColor;
    font-size: 14px;
    border: 1px solid $borderColor;
}
.error {
    .bf-content {
        border-color: $errorColor;   
    }
}
.hidden {
    display: none;
}
.normal-edit.bf-container {
    .bf-content {
        height: 110px;
    }
}
.small-edit.bf-container {
    .bf-content {
        height: 60px;
    }
    .public-DraftEditor-content {
        padding: 10px 5px;
    }
}
.link-btn {
    cursor: pointer;
    color: $normalBlue;
}

.reply-edit-container {
    .bf-controlbar {
        padding: 0;
        position: absolute;
        bottom: -36px;
        right: 60px;
        .control-item {
            margin: 0;
        }
        .control-item:not(:last-child) {
            margin-right: 3px;
        }
    }
    .reply-btn {
        float: right;
    }
}
.cropper-modal {
    opacity: .2;
}

.bofore-commit-tip {
    color: #ccc;
}

.title {
    color: $titleFontColor;
    font-size: $titleFontSize;
}
.underline {
    text-decoration: underline;
}
.underline:hover {
    text-decoration: underline;
}
.color-blue {
    color: $normalBlue;
}

